import React, { useEffect, useState } from "react";
import Chat, {
  Bubble,
  useMessages,
  Card,
  CardMedia,
  CardTitle,
  CardText,
  CardActions,
  Button,
} from "@chatui/core";
import "@chatui/core/dist/index.css";
import io from "socket.io-client";
import VideoPlayer from "./VideoPlayer";
let socket;

// const ENDPOINT = "localhost:5000";
const ENDPOINT = "https://locallearn.in/";

const initialMessages = [
  {
    type: "text",
    content: { text: "Hi, How are you today!" },
    user: {
      avatar: "https://avatars.dicebear.com/api/croodles-neutral/vibhavari.svg",
    }
  }
];
const defaultQuickReplies = [
  {
    icon: "message",
    name: "PlayVideoOne",
    isNew: true,
    isHighlight: true,
    url: "video1.mp4",
  },
  {
    name: "PlayVideoTwo",
    isNew: true,
    url: "video2.mp4",
  },
  {
    name: "PlayVideoThree",
    isHighlight: true,
    url: "video3.mp4",
  },
];
const ChatUI = () => {
  const { messages, appendMsg, setTyping } = useMessages(initialMessages);
  const [videourl, setVideourl] = useState("video1.mp4");
  const [connected, setConnected] = useState(false);

  useEffect(() => {
    socket = io(ENDPOINT);
    // console.log('socket');
    if (connected) {
      socket.emit(
        "user connected",
        {
          name: "shubham",
        },
        () => {
          setConnected(true);
        }
      );
    }
    // socket.emit('onlineStatus', { id: 'hi you are online' }, (response) => {
    //   console.log('response', response);
    // });
  }, []);

  useEffect(() => {
    socket.emit("onlineStatus", { id: "hi you are online" }, (response) => {
      console.log("response", response);
    });
    console.log("brodcast set ");
    socket.on("broadcast", (data) => {
      console.log("U  ----> ", data);
    });
    socket.on("send-msg-response", (data) => {
      console.log("response msg  ----> ", data);
      appendMsg({
        type: "text",
        content: { text: data },
        user: {
          avatar: "https://avatars.dicebear.com/api/croodles-neutral/vibhavari.svg",
        },
      });
    });
    return () => {
      // socket.emit('offlineStatus', { id: 'hi you are offline' }, () => {});
      // socket.emit('disconnect');
      socket.disconnect();
    };
  }, []);

  function handleSend(type, val) {
    if (type === "text" && val.trim()) {
      socket.emit("SendMessage", { text: val }, () => {});
      appendMsg({
        type: "text",
        content: { text: val },
	      user: {
          avatar: "https://avatars.dicebear.com/api/croodles-neutral/sukruti.svg"
        },
        position: "right",
      });

      // setTyping(true);

      // setTimeout(() => {
      //   appendMsg({
      //     type: 'text',
      //     content: { text: 'Ok Thanks for your Reply' },
      //     user: {
      //       avatar: 'locallearnlogo.svg',
      //     },
      //   });
      // }, 1000);
    }
  }

  function handleQuickReplyClick(item) {
    handleSend("text", item.name);
    setVideourl(item.url);
  }
  function handleToolbarClick(item) {
    console.log("toolbar", item);
    if (item.type === "orderSelector") {
      appendMsg({
        type: "order-selector",
        content: {},
      });
    }
  }

  function renderMessageContent(msg) {
    const { type, content } = msg;

    switch (type) {
      case "text":
        return <Bubble content={content.text} />;
      // return (
      //   <Card size="xl">
      //     <CardMedia image="https://cdn.pixabay.com/photo/2015/04/23/22/00/tree-736885__480.jpg" />
      //     <CardTitle>{content.text}</CardTitle>
      //     <CardText>{content.text}</CardText>
      //     <CardActions>
      //       <Button>No</Button>
      //       <Button color="primary">Yes</Button>
      //     </CardActions>
      //   </Card>
      // );
      case "image":
        return (
          <Bubble type="image">
            <img src={content.picUrl} alt="" />
          </Bubble>
        );
      default:
        return null;
    }
  }
  function NavbarClick(data) {
    console.log("yes pressed", data);
  }
  return (
    <div className="container h-100 ">
      <div className="row gx-0 h-100 ">
          <Chat
            navbar={{
              title: "Local Learn",
            }}
            style={{ height: "inherit" }}
            recorder={{ canRecord: true }}
            messages={messages}
            renderMessageContent={renderMessageContent}
            onSend={handleSend}
            placeholder="type..."
            locale="en-US"
          />
      </div>
	</div>
  )};

export default ChatUI;
